const inputValidator = (e) => {

    if (e.target.value.trim().length === 0) {
        e.target.previousElementSibling.innerText = 'Field is empty'
        e.target.previousElementSibling.style.color = '#B22222'
        e.target.style.border = "1px solid #B22222"
    }
    else {
        e.target.previousElementSibling.removeAttribute('style')
        e.target.previousElementSibling.innerText = `${e.target.placeholder}`
        e.target.removeAttribute('style')
    }
}

export const sortArray = (param = 'id', users) => {
    return [...users].sort((a, b) => {
        if (param === 'id') {
            return a[param] - b[param];
        }
        else if (param === 'name') {
            return (a[param].toLowerCase() > b[param].toLowerCase()) ? 1 : ((b[param].toLowerCase() > a[param].toLowerCase()) ? -1 : 0)
        }
        else if (param === 'city') {
            return (a.address[param].toLowerCase() > b.address[param].toLowerCase()) ? 1 : ((b.address[param].toLowerCase() > a.address[param].toLowerCase()) ? -1 : 0)
        }
        else if (param === "company"){
            return (a[param].name.toLowerCase() > b[param].name.toLowerCase()) ? 1 : ((b[param].name.toLowerCase() > a[param].name.toLowerCase()) ? -1 : 0)
        }
    })
}



export const nameChangeHandler = (e, user) => {
    inputValidator(e)

    return ({
        ...user,
        name: e.target.value
    })
}
export const emailChangeHandler = (e, user) => {
    inputValidator(e)

    return ({
        ...user,
        email: e.target.value
    })
}
export const cityChangeHandler = (e,user) => {
    inputValidator(e)

    return ({
        ...user,
        address: {
            ...user.address,
            city: e.target.value}
    })
}
export const companyChangeHandler = (e, user) => {
    inputValidator(e)

    return ({
        ...user,
        company: {
            ...user.company,
            name: e.target.value}})
}