import styled from 'styled-components'


export const NavBar = styled.nav`
  position: absolute;
  width: 100%;
  height: 98px;
  top: 0;
  background-color: rgba(255,255,255, 0.1);
  border: 1px solid rgba( 255, 255, 255, 0.2 );
  z-index: 100;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 30px;
  
  & span {
    color: white;
    align-self: flex-start;
    font-size: 2rem; 
    opacity: 0.3;  
    cursor: pointer;
  }
  & span:hover {
    opacity: 0.55;
  }
`;
